* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }
 
  
  body {
    font-size: 16px;
    font-family: 'cairo', sans-serif;
    line-height: 1.7;
    font-weight: bold;
    
     
       

    
  }
  .res-img {
    width: 100%;
  }
  
  .container {
    max-width: 1370px;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
  }
  
